export class Brand {
  id_thuong_hieu: any;
  ten_thuong_hieu: any;
  hinh: any;
  constructor(id_thuong_hieu, ten_thuong_hieu, hinh) {
    this.id_thuong_hieu = id_thuong_hieu;
    this.ten_thuong_hieu = ten_thuong_hieu;
    this.hinh = hinh;
  }
}



