export class MatKhau {
  mat_khau: any;
  re_mat_khau: any;
  current_password: any;
  constructor(mat_khau, re_mat_khau, current_password) {
    this.mat_khau = mat_khau;
    this.re_mat_khau = re_mat_khau;
    this.current_password = current_password;
  }
}



